<template>
  <div>
    <div class="top-container">
      <div class="top-left">
        <img :src="imageUrl.coverImgUrl" class="top-image" />
      </div>
      <div class="top-right">
        <div class="Boutique">精品歌单</div>
        <div class="short-intro">{{ imageUrl.name }}</div>
        <div class="introduce">
          {{ imageUrl.description }}
        </div>
      </div>
    </div>
    <div class="main-container">
      <!--标签分类列表-->
      <div class="classification">
        <span :class="{ highlighted: tag == '全部' }" @click="tag = '全部'">
          全部
        </span>
        <span :class="{ highlighted: tag == '欧美' }" @click="tag = '欧美'">
          欧美
        </span>
        <span :class="{ highlighted: tag == '华语' }" @click="tag = '华语'">
          华语
        </span>
        <span :class="{ highlighted: tag == '流行' }" @click="tag = '流行'">
          流行
        </span>
        <span :class="{ highlighted: tag == '说唱' }" @click="tag = '说唱'">
          说唱
        </span>
        <span :class="{ highlighted: tag == '摇滚' }" @click="tag = '摇滚'">
          摇滚
        </span>
        <span :class="{ highlighted: tag == '民谣' }" @click="tag = '民谣'">
          民谣
        </span>
        <span :class="{ highlighted: tag == '电子' }" @click="tag = '电子'"
          >电子</span
        >
        <span :class="{ highlighted: tag == '轻音乐' }" @click="tag = '轻音乐'">
          轻音乐
        </span>
        <span
          :class="{ highlighted: tag == '影视原声' }"
          @click="tag = '影视原声'"
          >影视原声
        </span>
        <span :class="{ highlighted: tag == 'ACG' }" @click="tag = 'ACG'">
          ACG
        </span>
        <span :class="{ highlighted: tag == '怀旧' }" @click="tag = '怀旧'">
          怀旧
        </span>
        <span :class="{ highlighted: tag == '治愈' }" @click="tag = '治愈'">
          治愈
        </span>
      </div>
      <!--歌曲分类列表-->
      <div>
        <ul class="classificationbox">
          <li v-for="(item, index) in mainList" :key="index">
            <div class="classificationbox_number">
              <i class="el-icon-video-play" @click="toListdetail(item.id)"> </i>
              <img :src="item.coverImgUrl" />
              <span>{{ item.name }}</span>
            </div>
          </li>
        </ul>
      </div>
      <!--分页组件-->
      <div>
        <el-pagination
          @current-change="handelClick"
          background
          layout="prev, pager, next"
          :total="total"
          :page-size="10"
          :current-page="page"
          class="pageClass"
        >
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      total: 0, //分页器总条数
      page: 1,
      imageUrl: {},
      tag: "全部",
      mainList: []
    };
  },
  watch: {
    tag() {
      //调用接口
      this.topdata();
      this.maindata();
      this.page = 1;
    }
  },
  created() {
    //调用接口
    this.topdata();
    this.maindata();
  },
  methods: {
    //封装推荐歌曲接口
    topdata() {
      axios({
        url: "https://autumnfish.cn/top/playlist/highquality",
        method: "get",
        params: {
          limit: 1,
          cat: this.tag
        }
      }).then(res => {
        this.imageUrl = res.data.playlists[0];
      });
    },
    //封装歌曲列表接口
    maindata() {
      axios({
        url: "https://autumnfish.cn/top/playlist/",
        method: "get",
        params: {
          limit: 10,
          offset: (this.page - 1) * 10,
          cat: this.tag
        }
      }).then(res => {
        this.total = res.data.total;
        this.mainList = res.data.playlists;
      });
    },
    handelClick(val) {
      this.page = val;
      this.maindata();
    },
    toListdetail(id) {
      this.$router.push(`/Listdetail?keywords=${id}`);
    }
  }
};
</script>

<style scoped>
/*top-container*/
.top-container {
  width: 1100px;
  height: 200px;
  background: #361c0f;
  border-radius: 10px;
  margin-left: 10px;
  display: flex;
}
.top-left {
  height: 200px;
  width: 200px;
  position: relative;
  float: left;
}
.top-image {
  height: 150px;
  width: 150px;
  border-radius: 5px;
  position: relative;
  top: 25px;
  left: 25px;
}
.top-right {
  flex: 1;
  height: 100%;
}
.Boutique {
  width: 100px;
  height: 30px;
  border: 1px solid #dba865;
  color: #dba865;
  line-height: 30px;
  text-align: center;
  border-radius: 5px;
  position: relative;
  top: 10px;
  left: 10px;
}
.short-intro {
  color: white;
  font-size: 18px;
  margin-top: 20px;
}
.introduce {
  color: #7a8482;
  font-size: 13px;
  margin-top: 10px;
  line-height: 18px;
  -webkit-line-clamp: 6;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  overflow: hidden;
  white-space: wrap;
  text-overflow: ellipsis;
}
/*main-container*/
.main-container {
  width: 100%;
}
.classification {
  float: right;
  margin-right: 55px;
  margin-top: 15px;
}
.classification span {
  margin-right: 10px;
  font-size: 14px;
  cursor: pointer;
}
.highlighted {
  color: #dd6d60;
}
/*歌单分类*/
.classificationbox {
  width: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-evenly;
}
.classificationbox li {
  margin: 10px 10px;
}
.classificationbox_number {
  width: 200px;
  height: 240px;
  font-size: 30px;
  color: #808080;
}
.classificationbox_number i {
  position: relative;
  left: 85px;
  top: 120px;
  opacity: 0;
}
.classificationbox_number:hover i {
  opacity: 1;
  cursor: pointer;
}
.classificationbox_number img {
  width: 200px;
  height: 200px;
  float: left;
}
.classificationbox_number span {
  display: block;
  max-width: 200px;
  overflow: hidden;
  font-size: 14px;
  text-overflow: ellipsis;
  white-space: nowrap;
  float: left;
}
/*分页器 */
.pageClass {
  margin-left: 300px;
  margin-top: 50px;
}
</style>



