<template>
  <div>
    <div class="top">
      <inputBox />
    </div>
    <div class="container">
      <div class="left-container">
        <Navigation />
      </div>
      <div class="right-container">
        <router-view />
      </div>
    </div>
    <div>
      <audio :src="musicurl" controls autoplay class="musicResouce"></audio>
    </div>
  </div>
</template>

<script>
import Navigation from "@/components/Navigation";
import inputBox from "@/components/inputBox";

export default {
  components: {
    Navigation,
    inputBox
  },
  data() {
    return {
      musicurl: ""
    };
  }
};
</script>

<style>
.container {
  width: 100%;
  display: flex;
}
.left-container {
  width: 200px;
  margin-top: -1px;
}
.right-container {
  flex: 1;
  height: 530px;
  overflow: hidden auto;
}
.musicResouce {
  width: 1360px;
  outline: none;
}
</style>



