<template>
  <div>
    <div class="Area">
      <span :class="{ highlighted: tag == 0 }" @click="tag = 0">全部</span>
      <span :class="{ highlighted: tag == 7 }" @click="tag = 7">华语</span>
      <span :class="{ highlighted: tag == 96 }" @click="tag = 96">欧美</span>
      <span :class="{ highlighted: tag == 8 }" @click="tag = 8">日本</span>
      <span :class="{ highlighted: tag == 16 }" @click="tag = 16">韩国</span>
    </div>
    <!--音乐标题-->
    <div class="list_title">
      <ul>
        <li>
          <div class="list_title_1">歌曲</div>
          <div class="list_title_2">歌手</div>
          <div class="list_title_3">专辑</div>
          <div class="list_title_4">时长</div>
        </li>
      </ul>
    </div>
    <!--音乐列表-->
    <div class="list_main">
      <ol>
        <li v-for="(item, index) in musicList" :key="index">
          <div class="number">{{ index + 1 }}</div>
          <div class="musicImage">
            <img :src="item.album.blurPicUrl" class="image" />
            <i class="el-icon-video-play" @click="playMusic(item.id)"></i>
          </div>
          <div class="musicname">{{ item.name }}</div>
          <div class="musicsinger">{{ item.album.artists[0].name }}</div>
          <div class="album">{{ item.album.name }}</div>
          <div class="musictime">{{ item.duration }}</div>
        </li>
      </ol>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      musicList: [],
      tag: 0 //音乐数组
    };
  },
  watch: {
    tag() {
      this.getmusicinfo();
    }
  },
  created() {
    this.getmusicinfo();
  },
  methods: {
    playMusic(id) {
      axios({
        url: "https://autumnfish.cn/song/url",
        method: "get",
        params: {
          id
        }
      }).then(res => {
        let url = res.data.data[0].url;
        this.$parent.musicurl = url;
      });
    },
    getmusicinfo() {
      axios({
        url: "https://autumnfish.cn/top/song",
        method: "get",
        params: {
          type: this.tag
        }
      }).then(res => {
        this.musicList = res.data.data;
        //处理时间
        for (var i = 0; i < this.musicList.length; i++) {
          let duration = this.musicList[i].duration;
          let min = parseInt(duration / 1000 / 60);
          let sec = parseInt((duration / 1000) % 60);
          if (min < 10) {
            min = "0" + min;
          }
          if (sec < 10) {
            sec = "0" + sec;
          }
          this.musicList[i].duration = `${min}:${sec}`;
        }
      });
    }
  }
};
</script>

<style scoped>
.Area {
  margin-bottom: 25px;
}
.Area span {
  font-size: 15px;
  margin-left: 30px;
  cursor: pointer;
}
.highlighted {
  color: #dd6d60;
}
/*音乐标题 */
.list_title {
  width: 100%;
}
.list_title ul li {
  display: flex;
  flex-direction: row;
  font-size: 13px;
  color: #909399;
}
.list_title_1 {
  flex-grow: 5;
  text-align: center;
  position: relative;
  left: 30px;
}
.list_title_2 {
  flex-grow: 2;
  text-align: center;
}
.list_title_3 {
  flex-grow: 3;
  text-align: center;
}
.list_title_4 {
  flex-grow: 2;
  text-align: center;
}
/*音乐列表 */
.list_main {
  width: 100%;
}
.list_main ol li {
  height: 100px;
  font-size: 13px;
  color: #606266;
  position: relative;
  margin-top: 50px;
}
.list_main ol li div {
  float: left;
}
.number {
  width: 50px;
}
.musicImage {
  width: 70px;
  height: 70px;
  text-align: center;
  position: relative;
  top: -20px;
  font-size: 25px;
}
.musicImage i {
  position: relative;
  top: -50px;
  opacity: 0;
}
.musicImage:hover i {
  opacity: 1;
  cursor: pointer;
}
.image {
  width: 70px;
  height: 70px;
  border-radius: 5px;
  float: left;
}
.musicname {
  width: 300px;
  text-align: center;
  position: relative;
  left: -5px;
}
.musicsinger {
  width: 200px;
  text-align: center;
  position: relative;
  left: 42px;
}
.album {
  text-align: center;
  width: 360px;
  position: relative;
  left: 10px;
}
.musictime {
  width: 100px;
  text-align: right;
  position: relative;
  left: -13px;
}
</style>



