import Vue from "vue";
import Router from "vue-router";
import foundMusic from "@/views/foundMusic";
import recommend from "@/views/recommend";
import newList from "@/views/newList";
import mvList from "@/views/mvList";
import result from "@/views/result";
import musicList from "@/views/musicList";
import Listdetail from "@/views/Listdetail";
import mvdetail from "@/views/mvdetail";
Vue.use(Router);
export default new Router({
  routes: [
    {
      path: "/",
      name: "foundMusic",  
      component: foundMusic
    },
    {
      path: "/recommend",
      name: "recommend",
      component: recommend
    },
    {
      path: "/newList",
      name: "newList",
      component: newList
    },
    {
      path: "/mvList",
      name: "mvList",
      component: mvList
    },
    {
      path: "/result",
      name: "result",
      component: result
    },
    {
      path: "/musicList",
      name: "musicList",
      component: musicList
    },
    {
      path: "/Listdetail",
      name: "Listdetail",
      component: Listdetail
    },
    {
      path: "/mvdetail",
      name: "mvdetail",
      component: mvdetail
    }
  ]
});



