<template>
  <div>
    <div class="top-video">
      <div class="top_info">
        <h2>MV</h2>
      </div>
      <video :src="mvUrl" class="video" controls autoplay></video>
    </div>
    <div>
      <h2>相关MV推荐</h2>
      <ul class="mvbox">
        <li v-for="(m, index) in mvs" :key="index">
          <div class="mvbox_number">
            <i class="el-icon-video-play" @click="playMV(m.id)"> </i>
            <img :src="m.cover" />
            <span class="MVname">{{ m.name }}</span>
            <span class="artistName">{{ m.artistName }}</span>
            <span class="number">播放次数:{{ m.playCount }}</span>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      mvUrl: "",
      mvs: []
    };
  },
  watch: {
    "$route.query.keywords"() {
      this.getMVurl();
    }
  },
  created() {
    this.getMVurl();
    //相关MV接口
    axios({
      url: "https://autumnfish.cn/simi/mv",
      method: "get",
      params: {
        mvid: this.$route.query.keywords
      }
    }).then(res => {
      console.log(res);
      this.mvs = res.data.mvs;
    });
  },
  methods: {
    //获取MV地址
    getMVurl() {
      axios({
        url: "https://autumnfish.cn/mv/url",
        method: "get",
        params: {
          id: this.$route.query.keywords
        }
      }).then(res => {
        console.log(res);
        this.mvUrl = res.data.data.url;
      });
    },
    playMV(id) {
      this.$router.push(`/mvdetail?keywords=${id}`);
    }
  }
};
</script>

<style scoped>
.video {
  width: 800px;
  height: 450px;
  outline-style: none;
  margin-top: 20px;
}
.top_info {
  width: 80%;
  display: flex;
  justify-content: space-between;
  margin-top: 10px;
  font-size: 18px;
  font-weight: bold;
}
.mvbox {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
}
.mvbox li {
  width: 200px;
  margin-top: 30px;
  margin-left: 15px;
}
.mvbox li img {
  width: 200px;
  height: 100px;
  border-radius: 5px;
}
.mvbox_number span {
  display: block;
  margin-top: 10px;
}
.MVname {
  font-size: 14px;
  display: block;
  max-width: 200px;
  overflow: hidden;
  font-size: 14px;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.artistName {
  font-size: 13px;
  color: #808080;
}
.number {
  font-size: 13px;
  color: #808080;
}
.MVtime {
  color: white;
  font-size: 14px;
  position: relative;
  top: -128px;
  left: 178px;
}
.mvbox_number {
  font-size: 25px;
}
.mvbox_number i {
  position: relative;
  left: 85px;
  top: 70px;
  opacity: 0;
}
.mvbox_number:hover i {
  opacity: 1;
  cursor: pointer;
}
</style>



