<template>
  <div>
    <!--顶部筛选-->
    <div class="screening">
      <ul>
        <li>
          <div class="area_screening">
            <label>地区</label>
            <span
              :class="{ highlighted: area == '全部' }"
              @click="area = '全部'"
            >
              全部
            </span>
            <span class="divider"></span>
            <span
              :class="{ highlighted: area == '内地' }"
              @click="area = '内地'"
            >
              内地
            </span>
            <span class="divider"></span>
            <span
              :class="{ highlighted: area == '港台' }"
              @click="area = '港台'"
            >
              港台
            </span>
            <span class="divider"></span>
            <span
              :class="{ highlighted: area == '欧美' }"
              @click="area = '欧美'"
            >
              欧美
            </span>
            <span class="divider"></span>
            <span
              :class="{ highlighted: area == '日本' }"
              @click="area = '日本'"
            >
              日本
            </span>
            <span class="divider"></span>
            <span
              :class="{ highlighted: area == '韩国' }"
              @click="area = '韩国'"
            >
              韩国
            </span>
          </div>
        </li>
        <li>
          <div class="area_screening">
            <label>类型</label>
            <span
              :class="{ highlighted: type == '全部' }"
              @click="type = '全部'"
              >全部</span
            >
            <span class="divider"></span>
            <span
              :class="{ highlighted: type == '官方版' }"
              @click="type = '官方版'"
              >官方版</span
            >
            <span class="divider"></span>
            <span
              :class="{ highlighted: type == '原声' }"
              @click="type = '原声'"
              >原声</span
            >
            <span class="divider"></span>
            <span
              :class="{ highlighted: type == '现场版' }"
              @click="type = '现场版'"
              >现场版</span
            >
            <span class="divider"></span>
            <span
              :class="{ highlighted: type == '网易出品' }"
              @click="type = '网易出品'"
              >网易出品</span
            >
          </div>
        </li>
        <li>
          <div class="area_screening">
            <label>排序</label>
            <span
              :class="{ highlighted: order == '上升最快' }"
              @click="order = '上升最快'"
              >上升最快</span
            >
            <span class="divider"></span>
            <span
              :class="{ highlighted: order == '最新' }"
              @click="order = '最新'"
              >最新</span
            >
            <span class="divider"></span>
            <span
              :class="{ highlighted: order == '最热' }"
              @click="order = '最热'"
              >最热</span
            >
          </div>
        </li>
      </ul>
    </div>
    <!--主体MV列表-->
    <div>
      <ul class="mvbox">
        <li v-for="(item, index) in mvlist" :key="index">
          <div class="mvbox_number">
            <i class="el-icon-video-play" @click="playMV(item.id)"> </i>
            <img :src="item.cover" />
            <span class="name">{{ item.name }}</span>
          </div>
          <span class="number">{{ item.playCount }}</span>
        </li>
      </ul>
    </div>
    <!--分页组件-->
    <div>
      <el-pagination
        @current-change="handelClick"
        background
        layout="prev, pager, next"
        :total="total"
        :page-size="8"
        :current-page="page"
        class="pageClass"
      >
      </el-pagination>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      total: 0,
      page: 1,
      area: "全部",
      type: "全部",
      order: "上升最快",
      mvlist: []
    };
  },
  watch: {
    area() {
      this.getMVlist();
      this.page = 1;
    },
    type() {
      this.getMVlist();
      this.page = 1;
    },
    order() {
      this.getMVlist();
      this.page = 1;
    }
  },
  created() {
    this.getMVlist();
  },
  methods: {
    getMVlist() {
      axios({
        url: "https://autumnfish.cn/mv/all",
        method: "get",
        params: {
          area: this.area,
          type: this.type,
          order: this.order,
          limit: 8,
          offset: (this.page - 1) * 8
        }
      }).then(res => {
        console.log(res);
        this.mvlist = res.data.data;
        //接口问题 有count就赋值
        if (res.data.count) {
          this.total = res.data.count;
        }

        for (var i = 0; i < this.mvlist.length; i++)
          if (this.mvlist[i].playCount > 10000) {
            this.mvlist[i].playCount =
              parseInt(this.mvlist[i].playCount / 10000) + "万";
          }
      });
    },
    handelClick(val) {
      this.page = val;
      this.getMVlist();
    },
    playMV(id) {
      this.$router.push(`/mvdetail?keywords=${id}`);
    }
  }
};
</script>

<style scoped>
.highlighted {
  color: red;
}
.screening li {
  margin-top: 20px;
}
.area_screening {
  width: 500px;
  height: 30px;
  display: flex;
  justify-content: space-between;
  color: #8090b5;
}
.area_screening label {
  line-height: 30px;
  color: black;
}
.area_screening span {
  font-size: 13px;
  line-height: 30px;
  cursor: pointer;
}
.divider {
  width: 1px;
  height: 20px;
  background: #8090b5;
  margin-top: 5px;
  opacity: 0.1;
}
/*mv列表 */
.mvbox {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  position: relative;
  left: -25px;
}
.mvbox_number {
  width: 240px;
  height: 150px;
  margin-top: 50px;
  font-size: 25px;
}
.mvbox_number span {
  display: block;
}
.mvbox_number img {
  width: 240px;
  height: 150px;
  border-radius: 10px;
  float: left;
}
.number {
  position: relative;
  top: -150px;
  left: 20px;
  color: white;
}
.name {
  font-size: 14px;
}
.pageClass {
  margin-left: 300px;
  margin-top: 50px;
}
.mvbox_number i {
  position: relative;
  left: 103px;
  top: 90px;
  opacity: 0;
}
.mvbox_number:hover i {
  opacity: 1;
  cursor: pointer;
}
</style>



