<template>
  <div>
    <!--轮播图-->
    <div>
      <el-carousel
        :interval="4000"
        type="card"
        height="200px"
        class="shuffling"
      >
        <el-carousel-item
          v-for="(item, index) in bannerList"
          :key="index"
          class="widerBorder"
        >
          <img :src="item.imageUrl" class="picture" />
        </el-carousel-item>
      </el-carousel>
    </div>
    <!--推荐歌单-->
    <h2>推荐歌单</h2>
    <div>
      <ul class="recommendedbox">
        <li v-for="(o, index) in recommendList" :key="index">
          <div class="box_number">
            <i class="el-icon-video-play" @click="toListdetail(o.id)"> </i>
            <img :src="o.picUrl" alt="" />
            <span>{{ o.name }}</span>
          </div>
        </li>
      </ul>
    </div>
    <!--最新歌单-->
    <h2>最新歌单</h2>
    <div>
      <ul class="newbox">
        <li v-for="(n, index) in newBoxLit" :key="index">
          <div class="newbox_number">
            <i class="el-icon-video-play" @click="playmusic(n.id)"></i>
            <img :src="n.picUrl" />
            <span class="songname">{{ n.name }}</span>
            <span class="singername">{{ n.song.artists[0].name }}</span>
          </div>
        </li>
      </ul>
    </div>
    <!--推荐MV-->
    <h2>推荐MV</h2>
    <div>
      <ul class="mvbox">
        <li v-for="(m, index) in mvList" :key="index">
          <div class="mvbox_number">
            <i class="el-icon-video-play" @click="playMV(m.id)"> </i>
            <img :src="m.picUrl" />
            <span class="name">{{ m.copywriter }}</span>
            <span class="artistName">{{ m.artistName }}</span>
            <span class="number">{{ m.playCount }}</span>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "foundMusic",
  data() {
    return {
      bannerList: [], //轮播图数组
      recommendList: [], //推荐歌单数组
      newBoxLit: [], //最新歌单数组
      mvList: [] //MV数组
    };
  },
  created() {
    //请求接口获取轮播图数据
    axios({
      url: "https://autumnfish.cn/banner",
      method: "get",
      limit: "10"
    }).then(res => {
      this.bannerList = res.data.banners;
    });

    //请求获取推荐歌单的图片接口
    axios({
      url: "https://autumnfish.cn/personalized",
      method: "get",
      params: {
        limit: 10
      }
    }).then(res => {
      this.recommendList = res.data.result;
    });
    //请求获取最新歌单的图片接口
    axios({
      url: "https://autumnfish.cn/personalized/newsong",
      method: "get"
    }).then(res => {
      this.newBoxLit = res.data.result;
    });
    //请求MV接口
    axios({
      url: "https://autumnfish.cn/personalized/mv",
      method: "get"
    }).then(res => {
      this.mvList = res.data.result;
    });
  },
  methods: {
    playmusic(id) {
      //调用音乐接口,实现音乐播放 id传参
      axios({
        url: "https://autumnfish.cn/song/url",
        method: "get",
        params: {
          id
        }
      }).then(res => {
        let url = res.data.data[0].url;
        this.$parent.musicurl = url;
      });
    },
    toListdetail(id) {
      this.$router.push(`/Listdetail?keywords=${id}`);
    },
    playMV(id) {
      this.$router.push(`/mvdetail?keywords=${id}`);
    }
  }
};
</script>

<style scoped>
.widerBorder {
  border-radius: 10px;
}
h2 {
  padding: 20px 10px;
}
span {
  display: block;
}
/*推荐歌单*/
.recommendedbox {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-evenly;
}
.recommendedbox li {
  margin: 10px 10px;
}
.box_number {
  width: 200px;
  height: 240px;
  font-size: 30px;
  color: #808080;
}
.box_number i {
  position: relative;
  left: 85px;
  top: 120px;
  opacity: 0;
}
.box_number:hover i {
  opacity: 1;
  cursor: pointer;
}
.box_number img {
  width: 200px;
  height: 200px;
  float: left;
}
.box_number span {
  display: block;
  max-width: 200px;
  overflow: hidden;
  font-size: 14px;
  text-overflow: ellipsis;
  white-space: nowrap;
  float: left;
}
/*最新歌单*/
.newbox {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}
.newbox_number {
  width: 100px;
  height: 100px;
  margin-right: 400px;
  margin-top: 20px;
  font-size: 30px;
  color: #808080;
}
.newbox_number i {
  position: relative;
  top: 70px;
  left: 33px;
  opacity: 0;
}
.newbox_number:hover i {
  opacity: 1;
  cursor: pointer;
}
.newbox_number img {
  width: 100px;
  height: 100px;
  float: left;
}
.newbox_number span {
  position: relative;
  left: 34px;
  top: 12px;
  margin-bottom: 40px;
  white-space: nowrap;
}
.songname {
  font-size: 15px;
  color: black;
}
.singername {
  font-size: 12px;
  color: #808080;
}
/*推荐MV*/
.mvbox {
  display: flex;
  justify-content: space-evenly;
}
.mvbox_number {
  width: 240px;
  height: 150px;
  font-size: 25px;
}
.mvbox_number img {
  width: 240px;
  height: 150px;
  border-radius: 10px;
}
.name {
  font-size: 13px;
}
.artistName {
  margin-top: 10px;
  font-size: 13px;
  color: #808080;
}
.number {
  position: relative;
  top: -200px;
  left: 180px;
  color: white;
  font-size: 13px;
}
.mvbox_number i {
  position: relative;
  left: 103px;
  top: 90px;
  opacity: 0;
}
.mvbox_number:hover i {
  opacity: 1;
  cursor: pointer;
}
</style>



