<template>
  <div>
    <!--头部信息-->
    <div class="top_info">
      <span>
        <h1>{{ $route.query.keywords }}</h1>
      </span>
      <span class="singerInfo">搜索到{{ total }}条</span>
    </div>
    <!--主体信息-->
    <div class="main_list">
      <el-tabs type="border-card" v-model="activeIndex">
        <!--歌曲组件-->
        <el-tab-pane label="歌曲" name="song">
          <div>
            <ul>
              <li class="music_list_title">
                <div class="number">序号</div>
                <div class="songs_name">歌曲</div>
                <div class="singer_name">歌手</div>
                <div class="album_name">专辑</div>
                <div class="songtime_name">时长</div>
              </li>
              <li
                class="music_list_main"
                v-for="(item, index) in musicList"
                :key="index"
                @dblclick="playMusic(item.id)"
              >
                <div class="number">{{ index + 1 }}</div>
                <div class="songs_name">{{ item.name }}</div>
                <div class="singer_name">{{ item.artists[0].name }}</div>
                <div class="album_name">{{ item.album.name }}</div>
                <div class="songtime_name">{{ item.duration }}</div>
              </li>
            </ul>
          </div>
        </el-tab-pane>
        <!--歌单组件-->
        <el-tab-pane label="歌单" name="songList">
          <div>
            <ul class="recommendedbox">
              <li v-for="(o, index) in playList" :key="index">
                <div class="box_number">
                  <i class="el-icon-video-play" @click="playMusicList(o.id)">
                  </i>
                  <img :src="o.coverImgUrl" alt="" />
                  <span class="recommendedboxName">{{ o.name }}</span>
                  <span class="playCount">播放次数:{{ o.playCount }}</span>
                </div>
              </li>
            </ul>
          </div>
        </el-tab-pane>
        <!--MV组件-->
        <el-tab-pane label="MV" name="MV">
          <div>
            <ul class="mvbox">
              <li v-for="(m, index) in mvList" :key="index">
                <div class="mvbox_number">
                  <i class="el-icon-video-play" @click="playMV(m.id)"> </i>
                  <img :src="m.cover" />
                  <span class="MVname">{{ m.name }}</span>
                  <span class="artistName">{{ m.artistName }}</span>
                  <span class="MVnumber">播放次数:{{ m.playCount }}</span>
                  <span class="MVtime">{{ m.duration }}</span>
                </div>
              </li>
            </ul>
          </div>
        </el-tab-pane>
      </el-tabs>
      <!--分页组件-->
      <div>
        <el-pagination
          @current-change="handelClick"
          background
          layout="prev, pager, next"
          :total="total"
          :page-size="5"
          :current-page="page"
          class="pageClass"
        >
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      activeIndex: "song",
      tag: "歌曲",
      total: 0,
      page: 1,
      musicList: [],
      playList: [],
      mvList: []
    };
  },
  created() {
    this.searchMusicClass();
  },
  watch: {
    activeIndex() {
      this.page = 1;
      this.searchMusicClass();
    },
    "$route.query.keywords": "searchMusicClass"
  },
  methods: {
    handelClick(val) {
      this.page = val;
      this.searchMusicClass();
    },
    searchMusicClass() {
      let type = 1;
      let limit = 10;
      switch (this.activeIndex) {
        case "song":
          type = 1;
          limit = 20;
          break;
        case "songList":
          type = 1000;
          limit = 15;
          break;
        case "MV":
          type = 1004;
          limit = 8;
          break;
        default:
          break;
      }
      axios({
        url: " https://autumnfish.cn/search",
        method: "get",
        params: {
          keywords: this.$route.query.keywords,
          limit,
          type,
          offset: (this.page - 1) * limit
        }
      }).then(res => {
        if (type == 1) {
          this.musicList = res.data.result.songs;
          for (var i = 0; i < this.musicList.length; i++) {
            let duration = this.musicList[i].duration;
            let min = parseInt(duration / 1000 / 60);
            let sec = parseInt((duration / 1000) % 60);
            if (min < 10) {
              min = "0" + min;
            }
            if (sec < 10) {
              sec = "0" + sec;
            }
            this.musicList[i].duration = `${min}:${sec}`;
            this.total = res.data.result.songCount;
          }
        } else if (type == 1000) {
          this.playList = res.data.result.playlists;
          this.total = res.data.result.playlistCount;
          for (var i = 0; i < this.playList.length; i++) {
            if (this.playList[i].playCount > 10000) {
              this.playList[i].playCount =
                parseInt(this.playList[i].playCount / 10000) + "万";
            }
          }
        } else {
          this.mvList = res.data.result.mvs;
          this.total = res.data.result.mvCount;
          for (var i = 0; i < this.mvList.length; i++) {
            if (this.mvList[i].playCount > 10000) {
              this.mvList[i].playCount =
                parseInt(this.mvList[i].playCount / 10000) + "万";
            }
            let min = parseInt(this.mvList[i].duration / 1000 / 60);
            if (min < 10) {
              min = "0" + min;
            }
            let sec = parseInt((this.mvList[i].duration / 1000) % 60);
            if (sec < 10) {
              sec = "0" + sec;
            }
            this.mvList[i].duration = `${min}:${sec}`;
          }
        }
      });
    },
    playMusic(id) {
      axios({
        url: "https://autumnfish.cn/song/url",
        method: "get",
        params: {
          id
        }
      }).then(res => {
        let url = res.data.data[0].url;
        this.$parent.musicurl = url;
      });
    },
    playMusicList(id) {
      this.$router.push(`/Listdetail?keywords=${id}`);
    },
    playMV(id) {
      this.$router.push(`/mvdetail?keywords=${id}`);
    }
  }
};
</script>

<style scoped>
/*头部信息样式*/
.top_info {
  margin-bottom: 50px;
  display: flex;
  flex-direction: row;
}
h1 {
  font-size: 25px;
  margin-top: 10px;
}
.singerInfo {
  color: #bebebe;
  font-size: 12px;
  margin-top: 25px;
  margin-left: 20px;
}
/*主体样式*/
.main_list ul li {
  margin-bottom: 20px;
  line-height: 30px;
}
.music_list_title div {
  display: inline-block;
  color: #bebebe;
  font-size: 14px;
}
.music_list_main div {
  color: black;
  font-size: 15px;
  display: inline-block;
  opacity: 0.8;
}
/*音乐头 */
.number_list {
  width: 100px;
  text-align: center;
}
.songs {
  width: 300px;
  text-align: left;
}
.singer_name {
  width: 150px;
  text-align: center;
}
.album_name {
  width: 300px;
  text-align: center;
}
.songtime_name {
  width: 200px;
  text-align: center;
}

/*音乐列表 */
.number {
  width: 100px;
  text-align: center;
}
.songs_name {
  width: 340px;
  text-align: left;
}
.singer_name {
  width: 150px;
  text-align: center;
}
.album_name {
  width: 300px;
  text-align: center;
}
.songtime_name {
  width: 200px;
  text-align: center;
}
/*分页器 */
.pageClass {
  margin-left: 300px;
  margin-top: 50px;
}
/*歌单*/
.recommendedbox {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-evenly;
}
.recommendedbox li {
  margin: 10px 10px;
}
.box_number {
  width: 200px;
  font-size: 30px;
  color: #808080;
}
.box_number i {
  position: relative;
  left: 85px;
  top: 120px;
  opacity: 0;
}
.box_number:hover i {
  opacity: 1;
  cursor: pointer;
}
.box_number img {
  width: 200px;
}
.recommendedboxName {
  font-size: 14px;
  display: block;
  max-width: 200px;
  overflow: hidden;
  font-size: 14px;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.playCount {
  font-size: 14px;
}
/*推荐MV*/
.mvbox {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-evenly;
}
.mvbox_number {
  font-size: 25px;
  width: 240px;
  margin-top: 30px;
}
.mvbox_number span {
  display: block;
}
.mvbox_number img {
  width: 240px;
  height: 150px;
  border-radius: 10px;
}
.MVname {
  font-size: 14px;
  display: block;
  max-width: 200px;
  overflow: hidden;
  font-size: 14px;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.artistName {
  font-size: 13px;
  color: #808080;
}
.MVnumber {
  font-size: 13px;
  color: #808080;
}
.MVtime {
  color: white;
  font-size: 14px;
  position: relative;
  top: -128px;
  left: 178px;
}
.mvbox_number i {
  position: relative;
  left: 110px;
  top: 95px;
  opacity: 0;
}
.mvbox_number:hover i {
  opacity: 1;
  cursor: pointer;
}
</style>



