<template>
  <div id="app">
    <index />
  </div>
</template>
<script>
import index from "@/components/index.vue";
export default {
  components: {
    index
  },
  created() {
    if (this.$router.path !== "/") {
      this.$router.replace("/");
    }
  }
};
</script>

<style>
* {
  margin: 0;
  padding: 0;
  user-select: none;
  list-style: none;
}
a {
  text-decoration: none;
}
</style>



