<template>
  <div class="top">
    <!--图标-->
    <div class="icongroup">
      <i class="el-icon-user-solid"></i>
      <i class="el-icon-more"></i>
      <i class="el-icon-star-off"></i>
    </div>

    <!--输入框-->
    <div class="Input-box">
      <el-input
        placeholder="请输入歌曲或歌手名称"
        v-model="input"
        clearable
        @keyup.enter.native="search"
        size="small"
      >
      </el-input>
    </div>
  </div>
</template>

<script>
export default {
  name: "top",
  data() {
    return {
      input: ""
    };
  },
  methods: {
    search() {
      if (this.input == "") {
        alert("请输入内容");
      } else {
        this.$router.push(`/result?keywords=${this.input}`);
      }
      this.input = "";
    }
  }
};
</script>

<style>
.top {
  height: 60px;
  background-color: #ddd6c1;
}
.icongroup {
  float: left;
  line-height: 60px;
  margin-left: 20px;
}
.icongroup i {
  margin-right: 20px;
}
.Input-box {
  width: 200px;
  margin-right: 70px;
  float: right;
  line-height: 60px;
}
</style>



