<template>
  <div>
    <!-- 头部信息-->
    <div class="top_info">
      <div class="image_box">
        <img :src="image_title" class="image_title" />
      </div>
      <div class="top_right_info">
        <p>{{ name }}</p>
        <div>
          <img src="" alt="" />
          <span></span>
        </div>
        <span class="title">
          标签:
          <ul>
            <li v-for="item in tags" :key="item">{{ item }}</li>
          </ul>
        </span>
        <div class="descprition">简介:{{ description }}</div>
      </div>
    </div>
    <!--主体信息-->
    <div class="main_info">
      <el-tabs v-model="activeName">
        <el-tab-pane label="歌曲列表" name="Song">
          <div>
            <!--音乐标题-->
            <div class="list_title">
              <ul>
                <li>
                  <div class="list_title_1">歌曲</div>
                  <div class="list_title_2">歌手</div>
                  <div class="list_title_3">专辑</div>
                  <div class="list_title_4">时长</div>
                </li>
              </ul>
            </div>
            <!--音乐列表-->
            <div class="list_main">
              <ol>
                <li v-for="(item, index) in musicList" :key="index">
                  <div class="number">{{ index + 1 }}</div>
                  <div class="musicImage">
                    <img :src="item.al.picUrl" class="image" />
                    <i
                      class="el-icon-video-play"
                      @click="playMusic(item.id)"
                    ></i>
                  </div>
                  <div class="musicname">{{ item.name }}</div>
                  <div class="musicsinger">
                    {{ item.ar[0].name }}
                  </div>
                  <div class="album">{{ item.al.name }}</div>
                  <div class="musictime">{{ item.dt }}</div>
                </li>
              </ol>
            </div>
          </div>
        </el-tab-pane>
        <el-tab-pane label="评论" name="say"></el-tab-pane>
      </el-tabs>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      image_title: "",
      name: "",
      tags: [],
      description: "",
      activeName: "Song",
      musicList: []
    };
  },
  created() {
    axios({
      url: " https://autumnfish.cn/playlist/detail",
      method: "get",
      params: {
        id: this.$route.query.keywords
      }
    }).then(res => {
      this.image_title = res.data.playlist.coverImgUrl;
      this.name = res.data.playlist.name;
      this.tags = res.data.playlist.tags;
      this.description = res.data.playlist.description;
    });
    this.getmusicinfo();
    this.playMusic();
  },
  methods: {
    playMusic(id) {
      axios({
        url: "https://autumnfish.cn/song/url",
        method: "get",
        params: {
          id
        }
      }).then(res => {
        let url = res.data.data[0].url;
        this.$parent.musicurl = url;
      });
    },
    getmusicinfo() {
      axios({
        url: "https://autumnfish.cn/playlist/detail",
        method: "get",
        params: {
          id: this.$route.query.keywords
        }
      }).then(res => {
        this.musicList = res.data.playlist.tracks;
        //处理时间
        for (var i = 0; i < this.musicList.length; i++) {
          let duration = this.musicList[i].dt;
          let min = parseInt(duration / 1000 / 60);
          let sec = parseInt((duration / 1000) % 60);
          if (min < 10) {
            min = "0" + min;
          }
          if (sec < 10) {
            sec = "0" + sec;
          }
          this.musicList[i].dt = `${min}:${sec}`;
        }
      });
    }
  }
};
</script>

<style scoped>
.top_info {
  display: flex;
  flex-direction: row;
  margin-top: 35px;
}
.image_box {
  width: 200px;
  height: 200px;
}
.image_title {
  width: 200px;
  height: 200px;
}
p {
  margin-left: 30px;
}
.top_right_info span {
  display: block;
  margin-top: 15px;
  margin-left: 30px;
}
.title ul {
  display: flex;
}
.title ul li {
  margin-top: 15px;
  margin-right: 20px;
}
.descprition {
  color: #7a8482;
  font-size: 13px;
  margin-top: 10px;
  margin-left: 30px;
  line-height: 18px;
  -webkit-line-clamp: 4;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  overflow: hidden;
  white-space: wrap;
  text-overflow: ellipsis;
}
/*音乐标题 */
.list_title {
  width: 100%;
}
.list_title ul li {
  display: flex;
  flex-direction: row;
  font-size: 13px;
  color: #909399;
}
.list_title_1 {
  flex-grow: 5;
  text-align: center;
  position: relative;
  left: 30px;
}
.list_title_2 {
  flex-grow: 2;
  text-align: center;
}
.list_title_3 {
  flex-grow: 3;
  text-align: center;
}
.list_title_4 {
  flex-grow: 2;
  text-align: center;
}
/*音乐列表 */
.list_main {
  width: 100%;
}
.list_main ol li {
  height: 100px;
  font-size: 13px;
  color: #606266;
  position: relative;
  margin-top: 50px;
}
.list_main ol li div {
  float: left;
}
.number {
  width: 50px;
}
.musicImage {
  width: 70px;
  height: 70px;
  text-align: center;
  position: relative;
  top: -20px;
  font-size: 25px;
}
.musicImage i {
  position: relative;
  top: -50px;
  opacity: 0;
}
.musicImage:hover i {
  opacity: 1;
  cursor: pointer;
}
.image {
  width: 70px;
  height: 70px;
  border-radius: 5px;
  float: left;
}
.musicname {
  width: 300px;
  text-align: center;
  position: relative;
  left: -5px;
}
.musicsinger {
  width: 200px;
  text-align: center;
  position: relative;
  left: 42px;
}
.album {
  text-align: center;
  width: 360px;
  position: relative;
  left: 10px;
}
.musictime {
  width: 100px;
  text-align: right;
  position: relative;
  left: -13px;
}
</style>



